.home {
    padding-top: 100px;
}

.product {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    padding: 30px 20px;
    grid-column-gap: 20px;
    grid-row-gap: 30px;
}

.product a {
    text-decoration: none;
    color: black;
}

.card {
    border: none;
    box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.15);
    transition: all 0.3s ease;
    padding: 10px;
    margin: 10px;
    border-radius: 50px;
    cursor: pointer;
    text-align: center;
}





img {
    width: 200px;
    height: 200px;
    padding: 20px;
}

.button {
    margin: 20px;
    padding: 10px;
    cursor: pointer;
    background-color: #0f3460;
    color: white;
    border-radius: 10px;
    height: 50px;
    width: 200px;
    align-items: center;
    font-size: 18px;
}

.button:hover {
    background-color: green;
    transition: all 0.3s ease;
}

.spinner {
    padding: 100px;
    text-align: center;
}

.title {
    font-size: larger;
    padding: 20px;
}

h1 {
    padding: 20px;
    text-align: left;
}


@media screen and (max-width: 1000px) {
    .product {
        grid-template-columns: repeat(2, 1fr);
    }
}

@media screen and (max-width: 768px) {
    .product {
        grid-template-columns: repeat(1, 1fr);
    }
}