.navbar {
    display: flex;
    position: fixed;
    width: 100%;
}

header {
    display: flex;
    height: 75px;
    align-items: center;
    display: flex;
    justify-content: space-between;
    width: 100%;
    background-color: #0f3460;
    height: 50px;
}

.logo-text {
    margin: 0;
    font-size: 32px;
    font-weight: bold;
    text-transform: uppercase;
    text-decoration: none;
    padding-left: 50px;
    color: white;
}

.nav {
    display: flex;
    justify-content: center;
}


.nav ul {
    display: flex;
    justify-content: space-between;
    padding-right: 50px;
    list-style: none;
    font-size: large;
}

.nav ul li {
    margin-right: 20px;
}

.nav ul li a {
    text-decoration: none;
    color: white;
}

li {
    text-decoration: none;
    color: white;
}

li:hover {
    color: rgb(241, 14, 14);
    cursor: pointer;
}

.nav ul li a:hover {
    color: rgb(241, 14, 14);
}


.arrow {
    margin-left: 5px;
    font-size: 10px;
}



.nav ul li {
    position: relative;
}

.dropdown-menu {
    position: absolute;
    top: 100%;
    left: 0;
    z-index: 1;
    display: none;
    padding: 0;
    margin: 0;
    background-color: #0f3460;
    border: 1px solid #ccc;
    border-top: none;
    width: 100%;
    text-align: left;
    border-radius: 10px;
}


.dropdown-menu li {
    display: block;
    width: 100%;
}

.dropdown-menu li a {
    display: block;
    padding: 10px 15px;
    text-decoration: none;
    color: #333;
}



li:hover .dropdown-menu {
    display: block;
}

.dropdown-menu li div {
    position: relative;
    color: black;
}

.dropdown-menu li div::after {
    content: '';
    position: absolute;
    top: 50%;
    right: -5px;
    transform: translateY(-50%);
    border-top: 5px solid transparent;
    border-bottom: 5px solid transparent;
    border-left: 5px solid #000;
}



.categoryProduct {
    color: black;
}


.divAndLogo {
    display: flex;
    align-items: center;
}

.logo {
    margin: 15px;
}

.mobile-menue-icon {
    display: none;

}

@media screen and (max-width:1000px) {

    .nav {
        display: flex;
        align-items: center;
    }

    .logo {
        display: block;
        top: 15px;
        float: left;
    }

    .nav-links li {
        display: none;
    }

    .nav ul {
        display: block;
        padding-right: 50px;
        list-style: none;
        font-size: large;
    }



    .nav-links-mobile {
        position: absolute;
        display: block;
        list-style: none;
        box-shadow: rgba(50, 50, 93, 0.23) 0 30px 30px -20px;
        left: 0;
        top: 10%;
        transition: all 0.5s ease-in-out;
        width: 100%;
        background-color: #0f3460;
        padding-top: 50px;
    }

    ul li {
        padding: 32px;
        width: 100%;
        transition: all 0.5s ease-in-out;
    }

    .mobile-menue-icon {
        position: absolute;
        display: block;
        right: 50px;
        font-size: 24px;
        font-weight: bolder;
        cursor: pointer;
        background: none;
        border: none;
        color: #fff;
    }
}