.cart {
    padding-top: 100px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
}

.your-cart {
    padding: 20px;
    text-align: left;

}

.quantity {
    padding: 20px;
    font-size: 30px;
}


.cart-item {
    margin-bottom: 20px;
}


.total-container {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 20px;
}

.total {
    display: flex;
    justify-content: space-between;
    padding: 30px;
    background-color: #0f3460;
    color: white;
    width: 50%;
    align-items: center;
}

.total p {
    font-size: 20px;
}



.order {
    padding: 20px;
    border: none;
    background-color: green;
    color: white;
    cursor: pointer;
    border-radius: 4px;
    font-size: 15px;
    transition: 0.2s all;
    font-size: 17px;
}

.order:hover {
    background-color: rgb(153, 153, 55);

}


.plus:hover,
.minus:hover {
    background-color: green;
}

.remove {
    background-color: rgb(181, 46, 46);
}

.remove:hover {
    background-color: rgb(241, 14, 14);
}

.plus,
.minus {
    width: 50px;
    font-size: larger;
    font-weight: bolder;
}

@media screen and (max-width: 768px) {
    .quantity {
        display: block;
    }
}