.footer-container {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
}

.footer {
    margin-top: auto;
    text-align: center;
    background-color: #f5f5f5;
    padding: 20px;
}


@media (max-width: 768px) {
    .footer {
        font-size: 14px;
        padding: 10px;
    }
}