.single-item {
    padding-top: 100px;
    text-align: center;
}

.item {
    display: block;
}


.title {
    font-size: 1.5em;
}

.description {
    padding: 5px;
    margin: 20px;
    font-size: larger;
}

.star {
    color: #ddd;
    margin-right: 3px;
}

.filled {
    color: gold;
}

@media screen and (max-width: 768px) {
    .item {
        margin: 20px;
    }
}